p {
  //font-family: "Roboto";
}
body {
  background: url(../img/background.svg);
  margin: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-repeat: no-repeat;
  background-size: cover;
}
#header {
    height: 104px;
    display: flex;
   justify-content: space-between;
  background: rgba(92, 86, 86, 0.8);
  #logo_section {
    width: 50%;
    display: flex;

    #logo {
      margin: 28px 0 28px 27px;
      height: 48px;
    }
    #header_text {
      display: flex;
      align-items: center;
      height: 100%;
      font: {
        size: 20px;
      }
      line-height: 23px;
      color: rgba(255, 255, 255, 0.7);
      width: 30%;
      margin-left: 4%;
    }
  }

    #login_section {
      display: flex;
      align-items: center;
      margin-right: 3%;
      #login_text {
        font: {
          size: 25px;
        }
        color: rgba(255, 255, 255, 0.7);
        line-height: 29px;
      }
      #login_icon {
        height: 50px;
        width: 50px;
        margin-right: 6%;
      }
    }
 }

#main_wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  //justify-content: space-around;
  flex: 1 0 auto;
  padding: 5%;
  padding-left: 10%;
  .search_box {
    flex-basis: 55%;
    max-height: 185px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    & + .cat_button > img {
      width: 100%;
      left: -5%;
    }

    .search_wrapper {
      position: relative;
      height: 70px;
      width: 80%;
      #cat_search {
        background: rgba(230, 218, 218, 0.6);
        width: 100%;
        height: 100%;
        box-shadow: 10px 10px 4px rgba(0, 0, 0, 0.25);
        padding-left: 2%;
        border: none;

        font-style: normal;
        font-weight: normal;
        font-size: 28px;
        color: rgba(255, 255, 255, 0.8) !important;

        &:focus {
          outline: none;
        }

        &::placeholder {
          font-style: normal;
          font-weight: normal;
          font-size: 28px;
          @media screen and (max-width: 1450px) {
            font-size: 25px;
          }
          @media screen and (max-width: 1250px) {
            font-size: 20px;
          }
          @media screen and (max-width: 600px) {
            font-size: 18px;
          }
          @media screen and (max-width: 500px) {
            font-size: 16px;
          }
          @media screen and (max-width: 420px) {
            font-size: 13px;
          }
          @media screen and (max-width: 350px) {
            font-size: 10px;
          }
          color: rgba(255, 255, 255, 0.8) !important;
        }
      }
      &::after {
        content: '';
        position: absolute;
        width: 42px;
        height: 42px;
        right: 25px;
        top: 13px;
        background: url(../img/search_icon.svg);
      }
    }

  }


  .cat_button {
    position: relative;
    cursor: pointer;
    flex-basis: 25%;
    //height: 200px;
    max-width: 200px;
    max-height: 185px;
    margin-bottom: 35px;
    padding: 10px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    background: rgba(211, 207, 207, 0.7);
    box-shadow: 10px 4px 4px rgba(0, 0, 0, 0.25), inset 10px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    &:not(:first-child):not(:nth-child(2)):not(:last-child) {
      margin-right: 9%;
    }
    &:nth-child(4) {
      margin-left: -9%;
    }

    & > img {
      position: absolute;
      left: -20%;
      height: 90%;
      bottom: 20%;
      width: 125%;
    }

    .cat_text {
      position: absolute;
      bottom: 5%;
      width: 90%;
      background: url(../img/btn_back.png);
      background: {
        repeat: no-repeat;
        size: 107%;
        position-y: 10%;
        position-x: 50%;
      }
      border: 1px solid #000000;
      box-sizing: border-box;
      filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
      border-radius: 20px;
      margin: 0;

      font-family: Oxygen Mono;
      font-style: normal;
      font-weight: normal;
      font-size: 30px;
      text-align: center;
      //color: #3A1A1A;
      color: black;
    }
  }
}

footer {
  background: #5C5656;
  height: 45px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  .footer_text {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 29px;
    color: rgba(240, 238, 238, 0.8);
  }
}

